
import XHeader from 'vux/src/components/x-header/index.vue'

import localStore from 'util/localStore'
export default {
data () {
return {
write: false,
pCitoutletproperty2: localStore.get('TUP_pInfo').pCitoutletproperty2
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
to2019 () {
this.$router.push({
path: '/estimate2019'
})
},
to2020 () {
this.$router.push({
path: '/estimate2020'
})
},
toDbTarget () {
this.$router.push({
path: '/dbTarget'
})
}
},
created () {
this.$tupHttp(this, 'manage-psi/profitCalculation/permission-all/checkReport', 'POST').then(res => {
this.write = res.data.valid
})
},
components: {
XHeader
}
}



